import React from 'react';
import {Helmet} from 'react-helmet';

import 'bootstrap/dist/css/bootstrap.css';
import './homepage.css';

import Navbar from './Navbar';
import Jumbotron from './Jumbotron';
import Projects from './Projects';
import CV from './CV';



const Portfolio: React.FC = () => {
    const url = new URL(window.location.href);
    const searchParams = url.searchParams;
    const dataParams = searchParams.get('redirect');


    
    const htmlContent = {
      'home': <Jumbotron/>,
      'about': <CV/>,
      'projects': <Projects/>,
      'default': <Jumbotron/>,
    };

    let bodyContent;
    switch (dataParams) {
      case 'home':
        bodyContent = htmlContent.home;
        break;
      case 'cv':
        return (
          <html>
            <body>
            <Navbar/>

            <div className="row min-vh-100 p-5 d-flex align-items-center justify-content-center">
              <div className="col-12 col-md-4 pt-5">
                <CV/>
              </div>
            </div>

          
          </body>
        </html>

        )
        break;
      default:
        bodyContent = htmlContent.default;
    }
  
    return (
      <html>
        <Helmet>
          <title>Portfolio</title>
        </Helmet>

        <body>
          <Navbar/>

          <div className="row min-vh-100 p-5">
          <div className="col-12 col-md-4">
            {htmlContent.home}
          </div>
          <div className="col-12 col-md-8">
            {htmlContent.projects}
          </div>
        </div>
        
        </body>

      </html>      
    );
}

export default Portfolio;
